<template>
  <div class="about">
    <Nav ref="nav"/>
    <div class="hero is-primary">
      <div class="hero-body">
        <div class="container">
          <h1 class="title is-size-1">Hi! {{ this.$cookies.get("username") }}</h1>
        </div>
      </div>
    </div>
    <div class="columns is-multiline is-vcentered">
      <div v-for="im in images" :key="im.id" class="column is-one-quarter">
        <div class="card">
          <div class="card-image">
            <figure class="image is-4by3">
              <!--              <img src="https://bulma.io/images/placeholders/1280x960.png" alt="Placeholder image">-->
              <img :src="im.url" alt="Placeholder image">
            </figure>
          </div>
          <div class="content has-text-centered">
            {{ im.word }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Nav from "@/components/partials/Nav";
import RoomService from "@/services/RoomService";
export default {
  name: "Personal",
  components: {Nav},
  data() {
    // Initialize the room object
    return {
      images: [],
      roomId: -1,
    }
  },
  async created() {
    let fetchImages = await RoomService.fetchImages();
    if (fetchImages.err === 0) {
      this.images = fetchImages.data;
    }
    let fetchRoomIn = await RoomService.fetchRoomIn();
    if(fetchRoomIn.err === 0) {
      this.roomId = fetchRoomIn.data;
      console.log("roomId:" + this.roomId);
    }
    if(this.roomId !== -1){
      this.$refs.nav.showBackRoomBtn(this.roomId);
    }
  },
}
</script>

<style lang="scss" scoped>
.org-description {
  margin-top: 50px;
}
</style>
